<template>
  <div class="echarts">
    <div ref="myEchart" class="my-echart" />
  </div>
</template>

<script>
  export default {
    props: {
      lineData: { type: Object, default: () => {} },
    },
    data() {
      return {}
    },
    methods: {
      drawBar() {
        const myCharts = this.echarts.init(this.$refs.myEchart)
        const option = {
          title: {
            text: 'Line',
            left: 'center',
          },
          xAxis: {
            data: ['01', '02', '03', '04', '05', '06'],
            axisLabel: {
              interval: 0, // 强制显示x轴上所有标签
            },
          },
          yAxis: {
            splitLine: {
              // 坐标轴分割线设置
              show: false,
            },
            axisLine: {
            //坐标轴线设置
            },
            axisTick: {
            //刻度线设置
            },
            axisLabel: {
            //轴上的标签设置
            },
          },
          series: [
            {
              name: '时长',
              type: 'line',
              symbol: 'none', //标记的图形
              symbolSize: 4, //标记大小
              data: [5, 20, 36, 10, 10, 20],
            },
          ],
        }
        myCharts.setOption(option)
        window.addEventListener('resize', () => {
          myCharts.resize()
        })
      },
    },
    mounted() {
      this.drawBar()
    },
  }
</script>
