<template>
  <div class="echarts">
    <div ref="myEchart" style="height: 100%" />
  </div>
</template>

<script>
  export default {
    props: {
      barData: { type: Object, default: () => {} },
    },
    data() {
      return {}
    },
    methods: {
      drawBar() {
        // console.log(this.$refs.myEchart)
        const myCharts = this.echarts.init(this.$refs.myEchart)
        const option = {
          title: {
            text: 'bar',
            left: 'center',
          },
          xAxis: {
            data: ['oppo', 'Lenovo', 'MI', 'HuaWei', 'MEIZU', 'VIVO'],
            interval: 0,
            axisLabel: {
              interval: 0, // 强制显示x轴上所有标签
            },
          },
          yAxis: {
            min: 0,
            max: 100,
            splitNumber: 5,
            splitLine: {
              // 坐标轴分割线设置
              show: false,
            },
            axisLabel: {
              formatter: (value) => {
                return `${value}%`
              },
            },
          },
          series: [
            {
              name: '销量',
              type: 'bar',
              data: [5, 20, 36, 10, 10, 20],
              barMaxWidth: 15, // 柱条的最大宽度
            },
          ],
        }
        myCharts.setOption(option)
        window.addEventListener('resize', () => {
          myCharts.resize()
        })
      },
    },
    mounted() {
      this.drawBar()
    },
  }
</script>
