<template>
  <div class="echarts">
    <div ref="myEchart" class="my-echart" />
  </div>
</template>

<script>
  export default {
    props: {
      pieData: { type: Object, default: () => {} },
    },
    data() {
      return {}
    },
    methods: {
      drawBar() {
        const myCharts = this.echarts.init(this.$refs.myEchart)
        const option = {
          title: {
            text: 'pie',
            left: 'center',
          },
          series: [
            {
              name: '分布图',
              type: 'pie',
              data: [
                { name: 'VIVO', value: 80 },
                { name: 'HuaWei', value: 100 },
                { name: 'MI', value: 50 },
                { name: 'oppo', value: 20 },
              ],
            },
          ],
        }
        myCharts.setOption(option)
        window.addEventListener('resize', () => {
          myCharts.resize()
        })
      },
    },
    mounted() {
      this.drawBar()
    },
  }
</script>
